import React, { useState, useRef, useEffect } from 'react';
import ReactCrop, { centerCrop, makeAspectCrop } from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';
import styles from './ImageCropper.module.scss';
import classNames from 'classnames/bind';

const cx = classNames.bind(styles);

const ImageCropper = ({ imageFile, onImageCropped, onCancel }) => {
  const [crop, setCrop] = useState();
  const [imgSrc, setImgSrc] = useState('');
  const imgRef = useRef(null);

  // c file nh v chuyn i thnh URL  hin th
  useEffect(() => {
    if (!imageFile) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      setImgSrc(e.target.result);
    };
    reader.readAsDataURL(imageFile);

    return () => {
      if (imgSrc) {
        URL.revokeObjectURL(imgSrc);
      }
    };
  }, [imageFile]);

  // Thit lp crop mc nh thnh hnh vung khi nh c ti
  const onImageLoad = (e) => {
    const { width, height } = e.currentTarget;
    const cropWidthInPercent = (width < height ? 100 : (height / width) * 100);
    const cropHeightInPercent = (height < width ? 100 : (width / height) * 100);
    
    const crop = centerCrop(
      makeAspectCrop(
        {
          unit: '%',
          width: cropWidthInPercent,
          height: cropHeightInPercent
        },
        1, // T l khung hnh (1 = hnh vung)
        width,
        height
      ),
      width,
      height
    );

    setCrop(crop);
    imgRef.current = e.currentTarget;
  };

  // Hm thc hin crop nh khi ngi dng nhn Apply
  const handleCropImage = () => {
    if (!imgRef.current || !crop) return;

    const canvas = document.createElement('canvas');
    const scaleX = imgRef.current.naturalWidth / imgRef.current.width;
    const scaleY = imgRef.current.naturalHeight / imgRef.current.height;
    
    const pixelRatio = window.devicePixelRatio || 1;
    const cropWidth = crop.width * scaleX;
    const cropHeight = crop.height * scaleY;

    canvas.width = cropWidth * pixelRatio;
    canvas.height = cropHeight * pixelRatio;
    
    const ctx = canvas.getContext('2d');
    
    ctx.scale(pixelRatio, pixelRatio);
    ctx.imageSmoothingQuality = 'high';
    
    // Tnh ton ta  crop
    const cropX = crop.x * scaleX;
    const cropY = crop.y * scaleY;
    
    ctx.drawImage(
      imgRef.current,
      cropX,
      cropY,
      cropWidth,
      cropHeight,
      0,
      0,
      cropWidth,
      cropHeight
    );

    // Chuyn i canvas thnh blob
    canvas.toBlob((blob) => {
      if (!blob) {
        console.error('Canvas is empty');
        return;
      }
      
      // To file mi t blob  crop
      const croppedFile = new File([blob], imageFile.name, {
        type: imageFile.type,
        lastModified: new Date().getTime()
      });
      
      // Gi hm callback vi file  crop
      onImageCropped(croppedFile);
    }, imageFile.type);
  };

  return (
    <div className={cx('cropper-container')}>
      <div className={cx('crop-instruction')}>
        Ko hoc thay i kch thc  chn vng cn crop thnh hnh vung
      </div>
      
      {imgSrc && (
        <ReactCrop
          crop={crop}
          onChange={(c) => setCrop(c)}
          aspect={1}
          circularCrop={false}
        >
          <img 
            src={imgSrc} 
            onLoad={onImageLoad} 
            alt="Crop preview" 
            style={{ maxHeight: '400px' }}
          />
        </ReactCrop>
      )}
      
      <div className={cx('cropper-controls')}>
        <div className={cx('cropper-buttons')}>
          <button 
            className={cx('cropper-button', 'cancel-button')} 
            onClick={onCancel}
          >
            Hy
          </button>
          <button 
            className={cx('cropper-button', 'apply-button')} 
            onClick={handleCropImage}
          >
            p dng
          </button>
        </div>
      </div>
    </div>
  );
};

export default ImageCropper;