import React, { useContext, useEffect, useRef, useState } from "react";
import styles from "./Upload.module.scss";
import classNames from "classnames/bind";
import { toast } from "react-toastify";
import PropTypes from "prop-types";

import { AuthContext } from "~/contexts/AuthContext";
import constants from "~/services/constants";
import images from "~/assets/images";
import LoginModal from "../Modals/Login/LoginModal";
import ImageCropper from "../ImageCropper";
import * as miscFuncs from "~/helper/misc-functions";
import * as lockerService from "~/services/locketService";
import Help from "../Modals/Login/Help";
const cx = classNames.bind(styles);

const Upload = () => {
    const { user, setUser, isLoading } = useContext(AuthContext);

    const [file, setFile] = useState(null);
    const [caption, setCaption] = useState("");
    const [previewUrl, setPreviewUrl] = useState("");
    const [isShowModal, setIsShowModal] = useState(false);
    const [isUploading, setIsUploading] = useState(false);
    const [processingStatus, setProcessingStatus] = useState("");
    const [processingToastId, setProcessingToastId] = useState(null);
    const [processingProgress, setProcessingProgress] = useState(0);
    const [showCropper, setShowCropper] = useState(false);
    const [originalFile, setOriginalFile] = useState(null);
    
    const fileRef = useRef(null);
    const uploadAreaRef = useRef(null);

    useEffect(() => {
        return () => {
            if (previewUrl) {
                URL.revokeObjectURL(previewUrl);
            }
        };
    }, [previewUrl]);

    // Thm event listener  x l s kin paste (Ctrl+V)
    useEffect(() => {
        const handlePaste = async (e) => {
            if (!user || isUploading) return; // Khng x l nu cha ng nhp hoc ang upload

            const items = e.clipboardData?.items;
            if (!items) return;

            let file = null;

            // Tm item cha hnh nh trong clipboard
            for (let i = 0; i < items.length; i++) {
                if (items[i].type.indexOf('image') !== -1) {
                    file = items[i].getAsFile();
                    break;
                }
            }

            if (file) {
                e.preventDefault(); // Ngn chn hnh ng mc nh ca paste

                try {
                    setIsUploading(true);
                    setProcessingStatus("ang chun b x l nh t clipboard...");
                    
                    // Hin th toast thng bo ang x l
                    const toastId = toast.info("ang x l nh t clipboard...", {
                        ...constants.toastSettings,
                        autoClose: false,
                    });
                    setProcessingToastId(toastId);
                    
                    // i tn file  d nhn bit ngun gc
                    const renamedFile = new File([file], `clipboard_image_${new Date().getTime()}.png`, {
                        type: file.type,
                        lastModified: new Date().getTime()
                    });
                    
                    // X l file vi hm callback cp nht trng thi
                    const processedFile = await miscFuncs.processMedia(renamedFile, updateProcessingStatus);
                    
                    const objectUrl = URL.createObjectURL(processedFile);
                    setFile(processedFile);
                    setPreviewUrl(objectUrl);
                    
                    // Thng bo hon tt x l file
                    toast.update(toastId, {
                        render: ` x l nh thnh cng: ${(processedFile.size/1024/1024).toFixed(2)}MB`,
                        type: toast.TYPE.SUCCESS,
                        ...constants.toastSettings,
                        autoClose: 3000,
                    });
                    
                    setProcessingToastId(null);
                    setIsUploading(false);
                } catch (error) {
                    if (processingToastId) {
                        toast.dismiss(processingToastId);
                    }
                    toast.error(error.message, {
                        ...constants.toastSettings,
                    });
                    setProcessingToastId(null);
                    setIsUploading(false);
                }
            }
        };

        // Ch gn event listener khi  ng nhp
        if (user) {
            document.addEventListener('paste', handlePaste);
        }

        return () => {
            document.removeEventListener('paste', handlePaste);
        };
    }, [user, isUploading, processingToastId]);

    // Hin th thng bo tin trnh x l media
    const updateProcessingStatus = (status) => {
        setProcessingStatus(status);
        
        // Cp nht gi tr tin  nu c thng tin phn trm
        if (status.includes('%')) {
            try {
                const percentMatch = status.match(/(\d+)%/);
                if (percentMatch && percentMatch[1]) {
                    setProcessingProgress(parseInt(percentMatch[1]));
                }
            } catch (error) {
                console.error('Li phn tch tin :', error);
            }
        }
        
        // Xc nh nu y l bc cui cng hoc bc cp nht thng thng
        const isCompletionStep = status.includes('thnh cng') || status.includes('hon tt') || status.includes('hon thnh');
        const isInitialStep = status.includes('chun b') || status.includes('bt u');
        
        // Ch cc bc chnh  gia qu trnh mi cn hin th lu
        const shouldStayOpen = !isCompletionStep && !isInitialStep && status.includes('x l');
        
        if (processingToastId) {
            toast.update(processingToastId, {
                render: status,
                type: isCompletionStep ? toast.TYPE.SUCCESS : toast.TYPE.INFO,
                ...constants.toastSettings,
                autoClose: shouldStayOpen ? false : 2000,
            });
            
            // Nu l bc hon thnh, t lch xa ID toast sau khi n t ng
            if (isCompletionStep || !shouldStayOpen) {
                setTimeout(() => {
                    setProcessingToastId(null);
                }, 2000);
            }
        } else {
            const id = toast.info(status, {
                ...constants.toastSettings,
                autoClose: shouldStayOpen ? false : 2000,
            });
            setProcessingToastId(id);
            
            // Ch lu ID nu thng bo s m lu
            if (!shouldStayOpen) {
                setTimeout(() => {
                    setProcessingToastId(null);
                }, 2000);
            }
        }
    };

    const handleAfterLogin = (userInfo) => {
        setIsShowModal(false);
        setUser(userInfo.user);

        toast.dismiss();
        toast.success("Login successfully", {
            ...constants.toastSettings,
        });
    };

    const handleTriggerUploadFile = () => {
        fileRef.current.click();
    };

    const handleSelectFile = async (e) => {
        const { files } = e.target;
        if (files?.length) {
            processSelectedFile(files[0]);
        }
    };

    const handleDragOver = (e) => {
        // Ngn chn hnh ng mc nh ca th HTML  cho php th file vo
        e.preventDefault();
    };

    const handleSelectFileFromDrop = async (e) => {
        e.preventDefault();
        const { files } = e.dataTransfer;
        if (files?.length) {
            processSelectedFile(files[0]);
        }
    };

    const handleUploadFile = () => {
        const fileType = file.type.includes("image") ? "image" : "video";
        if (file) {
            setIsUploading(true);
            
            // Hin th thng bo upload ang tin hnh
            toast.info(`Uploading ${fileType}...`, {
                ...constants.toastSettings,
                autoClose: false,
                toastId: 'uploading'
            });
            
            lockerService
                .uploadMedia(file, caption, showToastPleaseWait)
                .then((res) => {
                    if (res) {
                        setPreviewUrl("");
                        setFile(null);
                        setCaption("");
                        setIsUploading(false);

                        toast.dismiss('uploading');
                        toast.success(`Upload ${fileType} successfully`, {
                            ...constants.toastSettings,
                        });
                    }
                })
                .catch((error) => {
                    let message = error.userMessage || 
                        error?.response?.data?.error?.message ||
                        error.message ||
                        "Upload failed";

                    if (message === "Failed to upload image: Forbidden") {
                        message = `Your ${fileType} is exceeding the maximum size allowed, please try again with a smaller ${fileType}`;
                    }
                    
                    setIsUploading(false);
                    toast.dismiss('uploading');
                    toast.error(message, {
                        ...constants.toastSettings,
                        autoClose: 5000
                    });
                });
        }
    };

    const showToastPleaseWait = () => {
        toast.dismiss();
        toast.info(
            "I just migrated the server to Render at the free version, so the request may take a longer time. Please wait for it. ",
            {
                ...constants.toastSettings,
            },
        );
    };

    // X l nh sau khi  crop
    const handleImageCropped = async (croppedFile) => {
        try {
            setShowCropper(false);
            setIsUploading(true);
            setProcessingStatus("ang x l nh  crop...");
            
            // Hin th toast thng bo ang x l
            const toastId = toast.info("ang x l nh  crop...", {
                ...constants.toastSettings,
                autoClose: false,
            });
            setProcessingToastId(toastId);
            
            // X l file  crop vi hm callback cp nht trng thi
            const processedFile = await miscFuncs.processMedia(croppedFile, updateProcessingStatus);
            
            const objectUrl = URL.createObjectURL(processedFile);
            setFile(processedFile);
            setPreviewUrl(objectUrl);
            
            // Thng bo hon tt x l file
            toast.update(toastId, {
                render: ` x l nh thnh cng: ${(processedFile.size/1024/1024).toFixed(2)}MB`,
                type: toast.TYPE.SUCCESS,
                ...constants.toastSettings,
                autoClose: 3000,
            });
            
            setProcessingToastId(null);
            setIsUploading(false);
            setOriginalFile(null);
        } catch (error) {
            if (processingToastId) {
                toast.dismiss(processingToastId);
            }
            toast.error(error.message, {
                ...constants.toastSettings,
            });
            setProcessingToastId(null);
            setIsUploading(false);
            setOriginalFile(null);
        }
    };
    
    // Hy qu trnh crop nh
    const handleCancelCrop = () => {
        setShowCropper(false);
        setOriginalFile(null);
        setIsUploading(false);
    };

    // Chun b file nh  crop
    const prepareImageForCrop = (imageFile) => {
        setOriginalFile(imageFile);
        setShowCropper(true);
    };

    // X l file sau khi chn
    const processSelectedFile = async (selectedFile) => {
        // Nu l nh, hin th cropper trc
        if (selectedFile.type.includes('image')) {
            prepareImageForCrop(selectedFile);
            return;
        }
        
        // Nu l video, x l bnh thng
        try {
            setIsUploading(true);
            setProcessingStatus("ang chun b x l video...");
            
            // Hin th toast thng bo ang x l
            const toastId = toast.info("ang chun b x l video...", {
                ...constants.toastSettings,
                autoClose: false,
            });
            setProcessingToastId(toastId);
            
            // X l file vi hm callback cp nht trng thi
            const processedFile = await miscFuncs.processMedia(selectedFile, updateProcessingStatus);
            
            const objectUrl = URL.createObjectURL(processedFile);
            setFile(processedFile);
            setPreviewUrl(objectUrl);
            
            // Thng bo hon tt x l file
            toast.update(toastId, {
                render: ` x l video thnh cng: ${(processedFile.size/1024/1024).toFixed(2)}MB`,
                type: toast.TYPE.SUCCESS,
                ...constants.toastSettings,
                autoClose: 3000,
            });
            
            setProcessingToastId(null);
            setIsUploading(false);
        } catch (error) {
            if (processingToastId) {
                toast.dismiss(processingToastId);
            }
            toast.error(error.message, {
                ...constants.toastSettings,
            });
            setProcessingToastId(null);
            setIsUploading(false);
        }
    };

    return (
        <div className={cx("wrapper")}>
            <div className={cx("card")}>
                {user ? (
                    <>
                        <h2 className={cx("title")}>Upload image or video</h2>
                        <div className={cx("input-container")}>
                            <input
                                type="text"
                                className={cx("post-title")}
                                placeholder="Enter the title for your post"
                                value={caption}
                                onChange={(e) => setCaption(e.target.value)}
                            />
                        </div>
                        
                        {/* Hin th trng thi x l nu ang x l */}
                        {isUploading && processingStatus && (
                            <div className={cx("processing-status")}>
                                <div className={cx("status-text")}>{processingStatus}</div>
                                <div className={cx("progress-bar")}>
                                    <div 
                                        className={cx("progress", { indeterminate: processingProgress === 0 })}
                                        style={{ width: processingProgress > 0 ? `${processingProgress}%` : undefined }}
                                    ></div>
                                </div>
                            </div>
                        )}
                        
                        <div
                            className={cx("upload-area")}
                            onDragOver={handleDragOver}
                            onDrop={handleSelectFileFromDrop}
                            role="button"
                            tabIndex="0"
                        >
                            {previewUrl ? (
                                <div className={cx("preview-wrapper")}>
                                    {file.type.includes("image") ? (
                                        <img
                                            src={previewUrl}
                                            alt="preview"
                                            className={cx("preview-image")}
                                        />
                                    ) : (
                                        <video
                                            src={previewUrl}
                                            alt="preview"
                                            className={cx("preview-video")}
                                            controls
                                        >
                                            <track
                                                kind="captions"
                                                src="captions.vtt"
                                                label="English"
                                            />
                                        </video>
                                    )}
                                    <button
                                        className={cx("btn-delete-preview")}
                                        onClick={() => setPreviewUrl("")}
                                    >
                                        <span>x</span>
                                    </button>
                                </div>
                            ) : (
                                <div className={cx("content")}>
                                    <button onClick={handleTriggerUploadFile}>
                                        <img
                                            src={images.mediaUpload}
                                            alt="upload"
                                            className={cx("upload-icon")}
                                        />
                                    </button>
                                    <h3>
                                        Drag and Drop file here or{" "}
                                        <button
                                            className={cx("underline")}
                                            onClick={handleTriggerUploadFile}
                                        >
                                            Choose file
                                        </button>
                                        <input
                                            type="file"
                                            ref={fileRef}
                                            onChange={handleSelectFile}
                                            accept="image/*,video/*"
                                        />
                                    </h3>
                                    <p className={cx("paste-hint")}>
                                        Hoc <kbd>Ctrl</kbd>+<kbd>V</kbd>  dn nh trc tip t clipboard
                                    </p>
                                </div>
                            )}
                        </div>
                        <div className={cx("actions")}>
                            <Help />
                            <div className={cx("buttons")}>
                                <button onClick={() => setPreviewUrl("")}>
                                    Cancel
                                </button>
                                <button
                                    disabled={
                                        previewUrl && caption && !isUploading
                                            ? ""
                                            : "disable"
                                    }
                                    className={cx("btn-submit", {
                                        "is-loading": isUploading,
                                    })}
                                    onClick={handleUploadFile}
                                >
                                    <span>Submit</span>
                                    {isUploading && (
                                        <img
                                            src={images.spinner}
                                            alt="spinner"
                                            className={cx("spinner")}
                                        />
                                    )}
                                </button>
                            </div>
                        </div>
                    </>
                ) : (
                    <div className={cx("no-login")}>
                        <h3>Please login to upload image or video</h3>
                        <button
                            className={cx("btn-login")}
                            onClick={() => setIsShowModal(true)}
                        >
                            Login here
                        </button>
                        <LoginModal
                            handleAfterLogin={handleAfterLogin}
                            show={isShowModal}
                            onHide={() => setIsShowModal(false)}
                            onPleaseWait={showToastPleaseWait}
                        />
                    </div>
                )}
            </div>
            {showCropper && originalFile && (
                <div className={cx("cropper-modal")}>
                    <div className={cx("cropper-overlay")}></div>
                    <div className={cx("cropper-content")}>
                        <ImageCropper
                            imageFile={originalFile}
                            onImageCropped={handleImageCropped}
                            onCancel={handleCancelCrop}
                        />
                    </div>
                </div>
            )}
        </div>
    );
};

export default Upload;
